.filter-checkbox {
  display: flex;

  &__input {
    position: absolute;
    opacity: 0;

    &:checked + .filter-checkbox__checkbox {
      &::after {
        display: block;
      }
    }

    &:hover + .filter-checkbox__checkbox::before,
    &:focus + .filter-checkbox__checkbox::before {
      background-color: $mercury;
    }
  }

  &__checkbox {
    position: relative;

    &::before {
      content: "";
      width: 56px;
      height: 56px;
      background-color: #f2f2f2;
      position: absolute;
      top: -16px;
      left: -80px;
      border-radius: 11%;
    }

    &::after {
      content: url("data:image/svg+xml,%3Csvg width='24' height='17' viewBox='0 0 24 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M22.925 1.325L21.825 0.225C21.525 -0.075 20.925 -0.075 20.625 0.225L8.725 12.125L2.525 5.925C2.225 5.625 1.625 5.625 1.325 5.925L0.225 7.025C-0.075 7.325 -0.075 7.925 0.225 8.225L8.025 16.025C8.225 16.225 8.425 16.225 8.625 16.225C8.825 16.225 9.025 16.125 9.225 16.025L22.725 2.525C23.225 2.225 23.225 1.725 22.925 1.325Z' fill='%23333333'/%3E%3C/svg%3E ");
      position: absolute;
      top: 2px;
      left: -62px;
      width: 20px;
      height: 20px;
      display: none;
    }
  }
}
